.project {
    width: 335px;
    height: 220px;
    background: transparent;
    color: white;
    display: flex;
    /* flex-direction: column; */
    text-align: center;
    align-items: center;
    justify-content: center;
    position: relative;
    backdrop-filter: blur(1px);
    background-image: linear-gradient(to bottom left, rgb(95, 95, 95), rgba(75, 75, 75, 0));
    background-size: 250% 250%;
    background-position: bottom left;
    border: 1px solid rgb(70, 70, 70);
    border-radius: 15px;
    transition: background-position 0.7s ease-in-out;
}

.project-link {
    text-decoration: none;
}

.project:hover {
    background-position: 30% 100%;
    border: 1px solid rgb(133, 133, 133);
}

.project-icon-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 18px;
    margin-right: 18px;
    gap: 18px;
}

.project-icon {
    width: 25px;
    height: auto;
    fill: white;
    color: white;
    position: absolute;
    top: 15px;
    right: 15px;
}

.project-title {
    font-family: "Poppins", sans-serif;
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
    width: 90%;
}

.project-skillset {
    width: 90%;
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.project-skillset span {
    display: inline-block;
    user-select: none;
    color: #40cf8e;
    outline: 1px solid #40cf8e;
    border-radius: 5px;
    font-size: 15px;
    padding: 5px;
    margin-right: 10px;
    margin-top: 10px;
    background-image: linear-gradient(to right, #40cf8e 50%, rgba(32, 32, 32, 0.0) 50%);
    background-size: 200% 100%;
    background-position: 100%;
    transition: background-position 0.5s ease, color 0.5s ease;
}

.project-skillset span:hover {
    background-position: 0%;
    color: black;
}

@media(max-width: 768px) {
    .project {
        /* width: auto; */
        width: 100%;
    }
}